.MathButton___24Elk {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25%;
    height: 100%;
    padding: 0;
    margin: 0;
    box-shadow: inset 1px 1px darkred;
    cursor: pointer;
    transition: 0.2s ease;
}

.MathButton___1A-uh {
    background: #ff5872;
    font-size: 30px;
    box-shadow: inset 0px 0px 23px 2px #ff9c9c;
}

.MathButton___1A-uh:hover{
    background: red;
    box-shadow: inset 0px 0px 5px 50px #858a43;
}

.MathButton___3MOub {
    background: #f3f9ff;
    font-family: Robotic;
    font-size: 50px;
    box-shadow: inset 0px 0px 25px 2px #bfbfbf;
}

.MathButton___3MOub:hover{
    background: #bfbfbf;
    box-shadow: inset 0px 0px 50px 10px #ff769a;
}

.MathButton___2J776 {
    box-shadow: inset 0px 0px 23px 1px #71b8c3;
    background: #cfd3e6;
    font-size: 35px;
}

.MathButton___2J776:hover{
    background: #c3970d;
    box-shadow: inset 0px 0px 15px 40px #16e67a;
}

.MathButton___24Elk:active{
    background: #158a91;
    box-shadow: inset 0px 0px 50px 5px black;
}